@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@300;400;500;600;700&display=swap);
body {
  margin: 0;
  font-family: 'Rubik',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow: hidden;
}

@media(max-width: 1100px){
  body{
    overflow-y: scroll;
  }
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.Calendar{display:flex;flex-direction:column;align-items:center;justify-content:center;margin-left:32px;padding:10% 15%;background-color:#272829;color:#DFDFDF;height:72vh}.Calendar__header{display:flex;width:100%;justify-content:space-between;align-items:center;font-size:19px;font-weight:500}.Calendar__btn{color:#DFDFDF;background-color:transparent;border:none;font-size:19px;font-weight:500;opacity:0.67}.Calendar__table{display:block;width:100%}.Calendar__tbody{display:block;border-top:1px solid #DFDFDF;padding-top:38px}.Calendar__thead{display:block}.Calendar__thead tr{display:grid;grid-template-columns:repeat(7, 1fr);text-align:center;font-size:19px;font-weight:500;padding:16px 0;margin-top:38px;border-bottom:1px solid #DFDFDF;border-top:1px solid #DFDFDF}.Calendar__tbody tr{display:grid;grid-template-columns:repeat(7, 1fr);font-size:17px;font-weight:500}.Calendar__day{display:block;padding:12px;cursor:pointer;text-align:center}.Calendar__day:hover{background-color:#353536;color:#DFDFDF}.Calendar__greyDay{opacity:0.24}.Calendar__today{background-color:#fdd000;color:#272829}@media (max-width: 1100px){.Calendar{height:auto;margin-left:0}}@media (max-width: 900px){.Calendar{padding:10% 5%;min-width:300px}}

.Popup__shadow{position:absolute;top:0;left:0;display:block;width:100vw;height:100%;background-color:#272829;opacity:0.85;z-index:2}.Popup__container{position:absolute;top:60%;left:50%;transform:translate(-50%, -50%);display:flex;flex-direction:column;align-items:flex-end;background-color:#FBFBFB;opacity:1;z-index:3}.Popup__btn{width:28px;height:28px;background-color:#fdd000;border:none}.Popup__labels{display:flex;padding:0 28px 25px}.Popup__label-box{display:flex;flex-direction:column;margin-right:20px}.Popup__label-box label{color:#A2A2A2;margin-bottom:8px}.Popup__label-box input{display:inline-block;border:1px solid #FDD000;padding:14px 20px}

.Home{display:flex;position:relative;justify-content:flex-end;align-items:center;min-height:80vh;background-size:cover}.Home__text{display:flex;flex-direction:column;align-items:flex-end}.Home__title{color:#fdd000;font-size:2.2rem;letter-spacing:3.5px;max-width:418px;text-transform:uppercase;margin-bottom:40px;text-align:right}.Home__paragraph{color:#DFDFDF;text-align:right;font-size:1.1rem;font-weight:300;letter-spacing:0.6px}.Home__calendar{width:35%;min-width:500px}@media (max-width: 600px){.Home{align-items:center;padding-left:0}.Home__calendar{min-width:300px}.Home__title{font-size:1.5rem}}@media (max-width: 1100px){.Home{display:flex;flex-direction:column;align-items:center}.Home__text{align-items:center}.Home__paragraph{display:none}.Home__title{color:#DFDFDF}.Home__title span{color:#fdd000}}

.About{padding:0 15%}.About__title{margin:40px 0;font-weight:bold;font-size:34px}.About__paragraphs{font-size:16px;font-weight:300;overflow-y:scroll}

.header{display:flex;justify-content:space-between;align-items:center;padding:0 5%}.header__logo{width:165px;height:50px;margin:0}.Nav__list{display:flex;list-style:none;padding:0}.Nav__item{display:block;padding:20px 0;margin-left:70px;cursor:pointer;transition:0.5s}.Nav__item:hover{transform:translateY(-5px)}.Nav__item a{font-size:24px;font-weight:500;letter-spacing:0.56px;text-decoration:none;color:#3d3d3d}@media (max-width: 600px){.Nav__item{margin-left:10%}.Nav__item a{font-size:18px}}

.App{color:#3d3d3d}

